.invoice-container{
    background: #fafafa;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.invoice-footer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: 40px;
}

.invoice-footer .btn-new-invoice{
    font-size: 13px;
}

.invoice-footer .approve-invoice-btn{
    background-color:#02bb02;
    margin-right: 20px;
}

.invoice-footer .approve-invoice-btn:hover{
    background-color: #0b911b!important;

}
.invoice-footer{
    background: #fff;
    padding: 8px 15px;
    border-top: 1px solid #ddd;
}


table{
    width: 100%;
    border: 1px solid #d6d6d6;
    margin-bottom: 20px;

}

