thead tr th{
    padding: 10px 3px 10px 5px;
    color: #999;
    background: linear-gradient(#fff,#fafafa);
    background-color: #fafafa!important;
}
table tbody{
    border-top: 1px solid #d6d6d6;
    background:white;
}

table tbody tr{
    border-bottom: 1px solid #d6d6d6;
}
