.tabel-footer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.price-section > div{
    padding: 5px 7px;
}

.price-section > div.total-invoice{
    border: 1px solid gray;
    border-radius: 3px;
    font-size: 18px;
    font-weight: 600;
    background: white;
}
.header-table-input{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 40px;
}

.invoice-input-currency{
    background: white;
}

.invoice-input-currency > div > div > div{
    padding: 12px 10px;
    background: white;
    font-size: 13px;
    padding-right: 31px;

}
#currency-menu ul li{
    font-size: 13px;
}

.dailog-for-currency *{
    overflow: visible!important;
}
#form-dialog-title{
    width: 500px;
}
.daialog-title h2{
    font-size: 22px;
    font-weight: 500;
}

.select-other-currency{
    width: 200px;
}
.dailog-for-currency .dialog-text{
    font-size: 14px;
    margin-bottom: 20px;
}

.dialog-btn button{
    font-size: 13px;
    margin-bottom: 10px;
}
.error-input{
    font-size: 12px;
    margin-top: 8px;
    color: red;
    display: inline-block;
}
.invoice-input label{
    font-size: 13px;
    -webkit-transform: translate(13px, 12px) scale(1);
            transform: translate(13px, 12px) scale(1);
}

.invoice-input input,
.invoice-input select{
    padding: 12px 10px;
    background: white;
    font-size: 13px;
    height: 14px;

}
.invoice-input select{
    height: 12px;
}

#invoice-branding{
    width: 125px;
}
.date-input-invoice{
    width: 140px;

}
.invoice-input > div > div{
    padding: 1px;
}
.id-input-invoice,
.ref-input-invoice{
    width: 130px;
}

.invoice-detail-new-container > div{
    margin-right: 10px;

}

.invoice-detail-new-container{
    padding-bottom: 10px;
    border-bottom: 1px solid #bcbcbc;
    margin-bottom: 19px;
}
